import React from 'react'
import FoodCard from './FoodCard'
import "./style.css"
import img1 from "./Images/quickfood1.jpg"
import img2 from "./Images/coupons.png"


export default function Menu() {
  return (
    <div>
      <div className="content">
        <div className="home_img">
          <img src={img1} alt="quick_food" className="quick_food_img"/>
        </div>
        <div className="menu-container">
          <FoodCard />
        </div> 
        <div className="slideshow_home">
          <img src={img2} />
        </div>
      </div>
    </div>

  )
}
